import React from "react";
import styles from "./Messages.module.css"
import usersPhoto from "./../../../../assets/Profile/usersProfileIcon.png"
import {useSelector} from "react-redux";

function Message(props) {
    const userId = useSelector(state => state.AuthPage.userId)
    return (
        <div>
            <div className={userId === props.userId ? styles.usersMessageBlock : styles.MessagesBlock}>
                <img className={styles.usersPhoto} src={props.usersPhoto != null ? props.usersPhoto : usersPhoto}/>
                <span className={styles.usersName}>
                    {props.name}
                </span>
                <div className={userId === props.userId ? styles.usersMessageBlockItem : styles.messageItem}>{props.message}</div>
            </div>
        </div>
    )
}

export default Message;