import React from "react";
import d from "./dialogs.module.css"
import DialogItem from "./DialogsComponents/Dialogs/DialogsItem/DialogsItem";
import Message from "./DialogsComponents/Messages/Message";
import SearchDialogs from "./DialogsComponents/Dialogs/SearchDialogs/SearchDialogs";
import {useSelector} from "react-redux";
import SendMessageArea from "./DialogsComponents/SendMsg/SendMsg";


function Dialogs() {
    const [Dialogs, Messages] = useSelector(state => [state.MsgPage.Dialogs, state.MsgPage.Messages])

    let DialogsElements = Dialogs.map(d => <DialogItem name={d.name} id={d.id} logo={d.logo} key={d.id}
                                                       lastmessage={d.LastMessage}
                                                       date={d.date}/>)
    let MessagesElements = Messages.map(m => <Message userId={m.userId} name={m.fullName} usersPhoto={m.usersPhoto}
                                                      message={m.message} id={m.id} key={m.id}/>)
    return <div className={d.dialogs}>
        <div>
            <SearchDialogs/>
            {DialogsElements}
        </div>
        <div>
            {MessagesElements}
            <SendMessageArea/>
        </div>
        <div>
        </div>
    </div>

}


export default Dialogs;